body{
    background-color: rgb(59, 106, 207);
    font-family: sans-serif;
    padding-left: 25%;

}
#container{
    width: 50%;
    display: flex;
    justify-content: center;
    background-color: rgb(231, 231, 231);
    border-radius: 15px;
}
button{
    padding: 15px;
    margin: 8px;
    font-family: sans-serif;
    Background-color: rgb(0, 247, 255);
    font-weight: 600;
    font-size: 17.5px ; 
    border: 0;
    border-radius: 10px; 
    outline: none;

}
button:hover{
    background-color: rgba(26, 219, 253, 0.911);
    cursor: pointer;
}
ul{
    padding: 0;
    margin: 0;
}
li{
    padding: 10px;
    padding-right: 20px;
    margin: 10px;
    color: rgb(255, 255, 255);
    background-color: rgb(77, 73, 73);
    list-style: none;
    font-size: 20px;
    border-radius: 9px;
    margin-bottom: 10px;
    transition:  background-color .2s ease-out;
}

li:hover{
    background-color: rgb(255, 0, 0);
    cursor: pointer;
}

input{
    width: 350px;
    height: 34px ;
    margin: 10px;
    line-height: 30px ;
    padding: 11px 19px ;
    font-size: 17.5px ; 
    border-radius: 10px;
    inset-inline: initial;
    border: 0;
    outline: none;
}